.bg-white {
  background-color: whitesmoke !important;
}

html,
body {
  margin: 0;
  padding: 0;
  background-color: #1e3345;
  color: black;
  /* overflow: auto; */
}

.myContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  text-align: left;
}

textarea {
  margin-top: 20vh;
  width: 50vw;
  min-height: 35vh;
  color: black;
}

#preview {
  width: 80vw;
  min-height: 60vh;
  overflow-y: scroll;
}

@media screen and (max-width: 850px) {
  textarea {
    margin-top: 20vh;
    width: 80vw;
    min-height: 40vh;
  }

  #preview {
    width: 90vw;
    min-height: 60vh;
    overflow-y: scroll;
  }
}
